  capitalizeFirstLetter =(string)=>{
    return string.charAt(0).toUpperCase()+string.slice(1);
  }
  constructor(props){
    super(props);
    console.log("Hello I am a constructor from News component");
    this.state={
      // articles:this.articles
      articles:[],
      loading:false,
      page:1

    }
    document.title=`${this.capitalizeFirstLetter(this.props.category)}-NewsMonkey}`
  }

  async componentDidMount(){
    console.log("cdm");
    let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=b8a64c874eb04772b999a9df634d9f8c&page-1&pageSize=${this.props.pageSize}`;
    let data=await fetch(url);
    let parseData=await  data.json()
    console.log(parseData);
    this.setState({articles:parseData.articles})
    
  }
  handlePrevClick=async()=>{
    let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=b8a64c874eb04772b999a9df634d9f8c &page=${this.state.page-1}&pageSize=${this.props.pageSize}`;
    this.setState({loading:true});  
    let data=await fetch(url);
      let parseData=await data.json()
      console.log(parseData);
      this.setState({
        page:this.state.page-1,
        articles:parseData.articles,
        loading:false
      })
  }


  handleNextClick=async()=>{
    console.log("Next");
    if(!(this.state.page+1>Math.ceil(this.state.totalResults/this.props.pageSize))){
    
      let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=b8a64c874eb04772b999a9df634d9f8c &page=${this.state.page+1}&pageSize=${this.props.pageSize}`;
      this.setState({loading:true});
      let data=await fetch(url);
      let parseData=await data.json()
      console.log(parseData);
      this.setState({
        page:this.state.page+1,
        articles:parseData.articles,
        loading:false
      })

     

    }
  }
  render() {
    
    return (
      <div className='container my-3'>
      {/* <h1 className='text-center'></h1> */}
        <h1 className='text-center m-5 text-danger'>NewsMonkey-Top {this.capitalizeFirstLetter(this.props.category)} Headlines</h1>
        {/* This is a News component */}
        {this.state.loading && <Spinner /> }
        
        <div className="row">

        {!this.state.loading && this.state.articles.map((element)=>{
        return <div className="col-md-4" key={element.url}>
        <NewsItem  title={element.title?element.title:""} description={element.description?element.description:""} imageUrl={element.urlToImage} newsUrl={element.url} author={element.author} date={element.publishedAt} source={element.source.name}/>
        </div>
      })} 
      <div>
        <div className='container d-flex justify-content-between'>
          <button disabled={this.state.page<=1} type="button" className='btn btn-dark' onClick={this.handlePrevClick} >&larr; Previous</button>
          <button disebled={this.state.page+1>Math.ceil(this.state.totalResults/this.props.pageSize)}type='button' className='btn btn-dark' onClick={this.handleNextClick}>Next &rarr;</button>
        </div>
      </div>
      </div>
        
        
      </div>
    )
  }
